/* slider */

.step-finish {
  .ant-steps-item-icon {
    background-color: green;
    border-color: green;
  }
  .ant-steps-item-title,
  .ant-steps-item-subtitle {
    color: green;
  }
}
.home-transition{
   white-space: nowrap;
   animation: 60s linear 0s infinite normal none running scrollText;
   -webkit-animation: 60s linear 0s infinite normal none running scrollText;
}

.step-process {
  .ant-steps-item-icon {
    background-color: blue;
    border-color: blue;
  }
  .ant-steps-item-title,
  .ant-steps-item-subtitle {
    color: blue;
  }
}

.step-wait {
  .ant-steps-item-icon {
    background-color: gray;
    border-color: gray;
  }
  .ant-steps-item-title,
  .ant-steps-item-subtitle {
    color: gray;
  }
}

.step-error {
  .ant-steps-item-icon {
    background-color: red;
    border-color: red;
  }
  .ant-steps-item-title,
  .ant-steps-item-subtitle {
    color: red;
  }
}
